<template>
<!-- 如果是左邊圖片的高度要放大 -->
    <div v-bind:id="content.type" class="content-body" v-bind:class="setHeightLeft()">  
        <!-- 判斷是左邊還是右邊 order要變換 -->
        <div v-bind:class="leftOrRight()" >
            <p class="title">{{content.title}}</p>
            <p class="content" v-html="content.content"></p>
        </div>
        <!-- 圖片區 -->
        <div class="content-img">
            <!-- 設置padding讓整塊置中 -->
            <img v-bind:src="setSrc()" v-bind:class="setPad()"> 
        </div>
    </div>
</template>


<script>


export default {
    data () {
        return {

        }
    },
    props: ['content'],
    methods: {
        // 如果是左邊就要將minheight 升高 現在沒用
        setHeightLeft(){
            if(this.content.type == "left"){return 'style-hight-left'}
        },
        // 圖片加個padding 分成左右兩邊
        setPad(){
            if(this.content.type == "left"){return 'style-left'}
            else{return 'style-right'}
        },
        // 判斷圖片是左邊還是右邊 要有不同style
        leftOrRight(){
            if(this.content.type === "left"){return 'left-content';}
            else{return 'right-content';}
        },
        // 設定圖片
        setSrc(){
            if(this.content.type === "right"){return require("../../assets/intro-vue.jpg");}
            else if(this.content.type === "left"){return require("../../assets/android.png");}
        }
    }
}
</script>

<style scoped>
/* 整個區塊大小 */
.content-body{
    display: flex;
    min-height: 30vw;
    margin-left: 4vw;
    margin-right: 4vw;
}
/* 如果是左邊圖片要變一下大小 */
/* .style-hight-left{
    min-height: 30vw;
} */
/* 右邊圖片的文字區 垂直排列 水平置中 字也是 */
.right-content{
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    text-align: center;
    align-items: center;
    color: black;
    background: #f8f8ff;
}
/* 左邊圖片的文字區 垂直排列 水平置中 字也是  圖片翻轉順序*/
.left-content{
    order: 1;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    text-align: center;
    align-items: center;
    color: black;
    background: #f8f8ff;
}
/* 標題區 水平排列 垂直置中 */
.title{
    display: flex;
    flex-grow: 1;
    align-items: center;
    font-size: 2.3vw;
    font-weight: bold;
    font-family: 'Literata' ,'serif';
    padding-top: 15px;
}
/* 內文區 */
.content{
    flex-grow: 1;
    font-size: 1.3vw;
    font-family: 'Noto Sans HK', sans-serif; 
    color: #a9a9a9;
}
/* 左右圖片 父元素區 */
.content-img{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
}
/* 左邊圖片 */
.style-left{
    width: 100%;
    height: 100%;
}
/* 右邊圖片 */
.style-right{
    width: 100%;
    height: 100%;
}

/* RWD 製作 */
@media all and (max-width: 1240px){
    .content-body{
        flex-direction: column;
        margin: 0;
        min-height: 60vw;
        background: #f8f8ff;
    }
    /* .style-hight-left{
        min-height: 60vw;
    } */
    .left-content{
        order: 0;
    }
    .title{
        font-size: 2.9vw;
    }
    .content{
        font-size: 1.9vw;
    }
    .content-img{
        width: 100%;
        background: #f8f8ff;
    }
    .style-left{
        height: 35vw;
        width: 60vw;
        display: block;
        padding: 0;
    }
    .style-right{
        height: 35vw;
        width: 60vw;
        display: block;
        padding: 0;
    }
}
</style>

