<template>
    <div class="photoContent">
        <fullImg v-bind:content="fullContent"></fullImg>
        <div class="img-body" v-for="(item, index) in content" :key="index">
            <div class="img-intro">
                <img src='../../../assets/add.png' v-on:click="showFullImg(index)">
            </div>
            <img v-bind:src="item.img">
            <!-- <img v-lazy="item.img"> -->
        </div>
    </div>
</template>

<script>

import fullImg from '../portfolio/displayFull'

export default {
    data () {
        return {
            fullContent:{clickImg:null,show:false},
        }
    },
    methods: {
        // 點擊加號出現滿版圖片
        showFullImg(index){
            this.fullContent.clickImg = this.content[index].img;
            this.fullContent.show = true;
            var mo=function(e){e.preventDefault();};//這是啥??
            document.body.style.overflow='hidden';//隱藏滾動條
            document.addEventListener("touchmove",mo,false);//禁止頁面滑動
        },
    },
    props:['content'],
    components:{
        fullImg,
    }
}
</script>

<style scoped>
.photoContent{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: 35vw;
    width: 85%;
    background: #fff;
    color: #2c3e50;
}
.img-body{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 20%;
    margin-left: 2%;
    height: 16.5vw;
    border-radius: 50vw;
    overflow: hidden;
    border-style: dashed;
    border-color: #f5deb3;
    padding: 0.4vw;
}
.img-intro{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 92%;
    height: 15vw;
    background: rgba(245,222,179,0);
    border-radius: 50vw;
    transition: 0.7s;
}
.img-intro > img{
    width: 0vw;
    height: 0vw;
    transition: 0.5s;
    cursor: pointer;
}
.img-body:hover .img-intro{
    background: rgba(245,222,179,0.8);
    transition: 0.5s;
}
.img-body:hover .img-intro > img{
    width: 3vw;
    height: 3vw;
    transition: 0.5s;
}
.img-body > img{
    width: 15vw;
    height: 15vw;
    border-radius: 50vw;
}
.img-body:nth-of-type(1){
    margin-left: 0;
}
.img-body:nth-of-type(5){
    margin-left: 0;
}
.img-body:nth-of-type(8){
    margin-left: 18vw;
}
.img-body:nth-of-type(9){
    margin-right: 18vw;
}
.img-body:nth-of-type(10){
    margin-left: 0;
}
@media all and (max-width: 1280px){
    .img-body{
        margin-left: 0;
        flex-basis: 22%;
        height: 18vw;
    }
    .img-body:nth-of-type(8){
        margin-left: 0vw;
    }
    .img-body:nth-of-type(9){
        margin-right: 0vw;
    }
    .img-body > img{
        width: 17vw;
        height: 17vw;
        border-radius: 50vw;
    }
    .img-intro{
        width: 17vw;
        height: 17vw;
    }
    .img-body:hover .img-intro > img{
        width: 4vw;
        height: 4vw;
    }
}
@media all and (max-width: 1080px){
    .img-body{
        margin-left: 0;
        flex-basis: 38%;
        height: 32vw;
        margin-top: 1vw;
    }
    .img-body:nth-of-type(8){
        margin-left: 0vw;
    }
    .img-body:nth-of-type(9){
        margin-right: 0vw;
    }
    .img-body > img{
        width: 30vw;
        height: 30vw;
        border-radius: 50vw;
    }
    .img-intro{
        width: 30vw;
        height: 30vw;
    }
    .img-body:hover .img-intro > img{
        width: 7vw;
        height: 7vw;
    }
}
</style>