<template>
    <div class="full-screen" v-if="content.show" v-on:click="hideFullImg()" v-bind:style="topStyle">
        <img v-bind:src="fullImg()">
    </div>
</template>

<script>
export default {
    data () {
        return {
            topStyle:{top: window.pageYOffset + 'px'},
        }
    },
    props:['content'],
    mounted(){
        window.addEventListener('scroll',this.handleScroll)
    },
    methods: {
        handleScroll(){
            this.topStyle = {top: window.pageYOffset + 'px'}
        },
        // 引藏滿版圖片
        hideFullImg(){
            this.content.show = false;
            var mo=function(e){e.preventDefault();};//這是啥??
            document.body.style.overflow='';//出現滾動條
            document.removeEventListener("touchmove",mo,false);//禁止頁面滑動
        },
        // 用來更換滿版圖片
        fullImg(){
            return this.content.clickImg;
        }
    },
    computed: {
        // 滿版圖片要出現在當下使用者的觀看位置
        setTop(){
            if(this.content.show == true){return{top: window.pageYOffset + 'px'}}
            else{return{top: 0}}
        },
    },
}
</script>

<style scoped>
/* 滿版圖片區 垂直和水平接置中 設置z-index才可以在螢幕最前 */
.full-screen{
    position: absolute;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1vw 0;
}
/* 滿版圖片大小 */
.full-screen > img{
    width: auto;
	height: auto;
	max-width: 100%;
	max-height: 100%;
}
</style>