<template>
    <article>
        <!-- 主要內容區 分成五區 在divList裡 -->
        <component v-for="(item, index) in divContent" :key="index" :is="chooseType(index)" v-bind:content="item"></component>
    </article>
</template>

<script>
// 三種樣式
import android from '../contentType/Android'
import day from '../contentType/Day'
import eightImg from '../contentType/EightImg'
import imgBg from '../contentType/imgBackground'

export default {
    data () {
        return {
            // 由上到下的網頁樣式
            divList:["day","android","android","day","imgBg","eightImg"],
            // 要傳給樣式Vue的物件
            divContent:[
                {
                    type:"day",
                    title:"You can make it!!",
                    content:"比你優秀的人還在努力<br>比你差的人也沒有放棄<br>那你又有什麼資格說你無能為力呢?"
                },
                {
                    type:"right",
                    title:"Vue",
                    content:"Vue的核心庫只關注視圖層 不僅易於上手<br>還便於與第三方庫或既有項目整合<br>另一方面 當與現代化的工具鏈以及各種支持類庫結合使用時<br>Vue也完全能夠為複雜的單頁應用提供驅動"
                },
                {
                    type:"left",
                    title:"android studio",
                    content:"Android Studio是一個為Android平台開發程式的整合式開發環境。<br>2013年5月16日在Google I/O上發布，可供開發者免費使用。<br>Android Studio基於JetBrains IntelliJ IDEA<br>為Android開發特殊客製化<br>並在Windows、OS X和Linux平台上均可執行。"
                },
                {
                    type:"self",
                    title:"Introduction",
                    content:"在還沒用盡全力前別急著說放棄"
                },
                {
                    type:"imgBg",
                    title:"Let one’s hair down",
                    content:"放鬆，做真實的自己"
                },
                {
                    type:"eightImg"
                }
            ]
        }
    },
    methods: {
        chooseType(index){
            return this.divList[index];
        }
    },
    components: {
        android,
        day,
        eightImg,
        imgBg
    }
}
</script>

