<template>
<!-- 判斷滾動的時候他要置頂 -->
    <header v-bind:class="{fix:isFix}">
        <!-- 左側滑欄區 -->
        <div class="left-menu">
            <!-- 左側滑欄內容 -->
            <div class="left-menu-content" v-bind:class="{ aniIn:isIn , aniOut:isOut}">
                <div class="line"></div>
                <!-- 左側滑欄內容menu -->
                <li v-for="(item, index) in menus" :key="index" class="left-menu-child" v-on:click="scrollToId(index)">
                    <a>{{item}}</a>
                </li>
            </div>
            <!-- 左側滑欄按鈕 -->
            <img class="menu-logo" src="../assets/list.png" v-on:click="openMenu()">
        </div>
        <!-- 左側滑欄區 -->
        <!-- logo 區 -->
        <div class="logo-container"> 
            <router-link :to="'home'"><img class="logo" src="../assets/self-logo.png"></router-link>
        </div>
        <!-- logo 區 -->
        <!-- 全螢幕menu區 -->
        <div class="menu">
            <!-- 全螢幕menu上方選單 -->
            <ul class="menu-title">
                <li v-for="(item, index) in menus" :key="index" 
                v-on:mouseover="currentMenu(index)">
                    <a v-on:click="scrollToId(index)">{{item}}</a>
                    <img class="imgMenu" src="../assets/logo.png">
                    <!-- 全螢幕menu點擊上方選單後出現下方選單 -->
                    <ul class="menu-child">
                        <div>
                        <li v-for="(item, index) in menuChilds" :key="index">
                            <a class="child">{{item}}</a>
                        </li>
                        </div>
                    </ul>
                </li>
            </ul>
        </div>
        <!-- 全螢幕menu區 -->
    </header>
</template>

<script>

export default {
    data () {
        return {
            menus:this.menu.title,//main menu
            connectHref:["/Android","/web","/work","/View","/educate"],
            totalMenu:[
                ["套件一","套件一","套件一"],
                ["頻道一","頻道一","頻道一"],
                ["音樂一","音樂一","音樂一"],
                ["景點一","景點一","景點一"],
                ["自傳一","自傳一","自傳一"]
            ],//total child menu
            scrollId:this.menu.link,
            menuChilds:[],//current child menu
            isFix:true,//控制header置頂
            isIn:false,//控制左側menu滑開
            isOut:false,//控制左側menu滑進去
        }
    },
    props:['menu'],
    mounted () {
        // 不能用this.handleScroll() 不知為啥 監聽滾動 要讓header置頂 現在沒用
        // window.addEventListener('scroll',this.handleScroll)

        // 監聽點擊螢幕 如果左側滑欄是開啟的 要關閉左側滑欄
        document.addEventListener("click",(e)=> {
            // 偵測是不是點到menu-logo 是的話就不觸發關閉menu
            let flag = e.target.contains(document.getElementsByClassName('menu-logo')[0]);
            if(flag){return;}
            else{ if(this.isIn == true){ this.closeMenu() } }  
        })
        window.addEventListener("resize",()=>{
            this.isIn = false;
            this.isOut = false;
        })
    },
    methods: {
        // 控制滑動時要讓Header置頂 現在沒用
        handleScroll(){
            // if(window.pageYOffset > 0){this.isFix = true;}
            // else{this.isFix = false;}
        },
        // 控制現在點到哪一個menu要出現那些child menu
        currentMenu(index){
            this.menuChilds = this.totalMenu[index]
        },
        scrollToId(index){
            this.closeMenu();  
            const yourElement = document.getElementById(this.scrollId[index]);
            const yCoordinate = yourElement.getBoundingClientRect().top + window.pageYOffset;
            const yOffset = -60; 

            window.scrollTo({
                top: yCoordinate + yOffset,
                behavior: 'smooth'
            });
            // window.scrollTo( 0, document.body.scrollHeight/this.scrollId[index] ); 大部分瀏覽器支援
            // window.scrollTo({
            //     top: document.body.scrollHeight/this.scrollId[index], 
            //     behavior: "smooth" 
            // });
        },
        setHref(index){
            return this.connectHref[index]
        },
        // 控制點擊menu-logo 開啟左側滑欄Menu
        openMenu(){
            if(this.isIn == true){this.closeMenu();}
            else{
                this.isOut = false;
                this.isIn = true;
            }
        },
        // 控制關閉左側滑欄Menu 要設定關閉menu後將aniOut remove掉 才不會一直播放動畫
        closeMenu(){
            this.isOut = true;
            this.isIn = false;
            setTimeout(()=>{ this.isOut = false;},500); 
            clearTimeout(0); 
        },
    },
}
</script>

<style scoped>
/* 讓他不會有前面的東西 */
li{
    list-style-type: none;
}
/* 當下滑的時候 header會固定在上面 */
.fix{
    width: 100vw;
    position: fixed;
}
/* 左側滑欄的區域 當縮小到一定程度就會出現 */
.left-menu{
    display: none;
    position: relative;
    flex-grow: 1;
    align-items: center;
    padding-left: 10px;
}
/* 左側滑欄和header中間要有一條線 */
.line{
    width: 100%;
    height: 1.5px;
    background: #d3d3d3;
    z-index: 1;
    margin-bottom: 2vw;
}
/* 左側滑欄內容區 要固定在head下面所以要用absolute 避免被圖片遮住要把z-index設成1 */
.left-menu-content{
    display: none;
    flex-direction: column;
    position: absolute;
    width: 35vw;
    height: 100vh;
    top: 4.8vw;
    background: white;
    z-index: 1;
}
/* 左側滑欄進入動畫 */
.aniIn{
    display: flex;
    left:0vw;
    animation: left-in;
    animation-duration: 0.5s; 
}
/* 左側滑欄出去動畫 */
.aniOut{
    display: flex;
    left:-35vw;
    animation: left-out;
    animation-duration: 0.5s; 
}
/* 左側滑欄內容按鈕區 */
.left-menu-child{
    display: flex;
    align-items: center;
    padding-left: 3.5vw;
    height: 4vw;
    width: 100%;
    cursor: pointer;
}
.left-menu-child > a{
    font-size: 1.5vw;
}
/* 左側滑欄內容按鈕碰觸背景變色 */
.left-menu-child:hover{
    background: #90ee90;
}
/* 左側滑欄內容按鈕碰觸字體變色 */
.left-menu-child:hover > a{
    color: white;
}
/* left-menu的圖片寬度 */
.menu-logo{
    width: 3.5vw;
    padding-left: 10px;
    cursor: pointer;
}
/* 網站的header標題 
    flex grow讓title佔 5 
    align-items justify-content 讓內元件主軸和交錯軸可以置中
*/
.logo-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 6;
    color: black;
}
/* self logo的寬度 */
.logo{
    width: 10vw;
    padding: 15px;
    margin-left: 2vw;
}
/* 網頁最大化的menu
    flex grow讓menu佔 11 
    align-items 讓它可以交錯軸置中
 */
.menu{
  display: flex;
  align-items: center;
  flex-grow: 10;
  color: black;
}
/* ul不用特別設置寬度 讓 li自適應內容寬度  在設置高度和display讓內元件可以置中*/ 
.menu-title{
    height: 100%;
    display: flex;
    /* align-items: center; */
    justify-content: space-around;
}
/* position relative 讓底下的ul可以參照  在設置高度和align-items: center 讓子元件垂直置中
還有讓底下出現的menu可以以top來固定在header 底部*/
ul.menu-title > li{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    float: left;
    margin-right: 3vw;
    font-weight: bold;
    white-space: nowrap; /* 讓 li自適應內容寬度 ul不用特別設置寬度*/
}
ul.menu-title a:hover {
    color: #90ee90;
}
/* 網頁最大化的menu圖片 */
.imgMenu{
    width: 15px;
    height: 15px;
    margin-left: 5px;
}
/* position absolute參照上面的 li */
ul.menu-title li > ul{
    display: none;
    position: absolute;
    z-index: 1;
    /* float: left; */
}
/* 現在沒用 */
/* 當觸碰到最上面的menu時 底下要出現 所以display改成flex還有 型式是column */
/* ul.menu-title li:hover > ul{
    display: flex;
    flex-direction: column;
    top: 100%;
    width: 15vw;
} */
/* 因為他是連結(a)所以display要改成block 還有不能被使用者選取和指標改成pointer */
.child{
    display: block;
    cursor: pointer;
    -webkit-user-select: none;
    padding: 20px;
    background: #f8f8ff;
}

/* 當小到某一程度時要變成側滑欄 */
@media all and (max-width: 1080px){
    /* 原本的menu要消失 */
    .menu{
        display: none;
    }
    /* 讓title變到中間一點 */
    .logo-container{
        flex-grow: 12;
        padding-right: 10%;
    }
    /* logo要放大 */
    .logo{
        padding: 0;
        width: 10vw;
        height: 7vw;
    }
    /* 左側滑欄menu要出現 */
    .left-menu{
        display: flex;
    }
    .left-menu-child > a{
        font-size: 2.2vw;
    } 
}
/* 當小到某一程度時要變成側滑欄 */
@media all and (max-width: 880px){
    .menu-logo{
        width: 4.5vw;
    }
    .logo{
        padding-top: 1vw;
        width: 13vw;
        height: 10vw;
    }
    .left-menu-child > a{
        font-size: 3vw;
    }
    .left-menu-content{
        top: 6.5vw;
        width: 50vw;
    }
    .left-menu-child{
        margin-top: 2vw;
    }
    /* 左側滑欄進入動畫 */
    .aniIn{
        left:0vw;
        animation: big-left-in;
        animation-duration: 0.5s; 
    }
/* 左側滑欄出去動畫 */
    .aniOut{
        display: flex;
        left:-50vw;
        animation: big-left-out;
        animation-duration: 0.5s; 
    }   
}
@media all and (max-width: 700px){
    .menu-logo{
        width: 5.5vw;
    }
      
}
</style>

