import Vue from 'vue'
// 創建  引入路由
import VueRouter from 'vue-router'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueLazyload from 'vue-lazyload'  //引入这个懒加载插件

Vue.use(VueLazyload)

// 或者添加VueLazyload 选项
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'dist/error.png',
  loading: 'dist/loading.gif',
  attempt: 1
})


// 使用
Vue.use(VueRouter)
Vue.use(ElementUI);

// 引入組件
import Header from './components/Header'

import Nav from './components/navContent/Nav';
import webNav from './components/navContent/selfWebNav'

import MainContent from './components/mainContent/MainContent';
import SelfWorkContent from './components/mainContent/SelfWorkContent';
import SelfEducateContent from './components/mainContent/SelfEducateContent';
import ViewContent from './components/mainContent/ViewContent';
import SelfWebContent from './components/mainContent/SelfWebContent';

// 實例化 && 定義路由
const router = new VueRouter({
  base:'#',
  routes: [
    { path: '/home',
      name:'home',
      components: {
        homeHeader: Header,
        MainContent: MainContent,
        Nav: Nav,
      }
    },
    { path: '/work',
      components: {
        Header: Header,
        MainContent: SelfWorkContent,
        Nav: Nav,
      }
    },
    { path: '/educate',
      components: {
        Header: Header,
        MainContent: SelfEducateContent,
        Nav: Nav,
      }
    },
    { path: '/View',
      components: {
        Header: Header,
        MainContent: ViewContent,
        Nav: Nav,
      }
    },
    { path: '/web',
      name:'web',
      components: {
        webHeader: Header,
        MainContent: SelfWebContent,
        webNav: webNav,
      }
    },
    {
      path: '*', 
      redirect: '/home'
    },
  ],
  scrollBehavior() {
    return {
      x: 0,
      y: 0,
    }
  }
})
Vue.config.productionTip = false

// 掛載路由
new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
