<template>
<!-- 整個區塊父元素 -->
    <div v-bind:id="content.type" class="content-body">
        <!-- 滿版圖片區 點擊會消失 -->
        <div class="full-screen" v-if="showFull" v-on:click="hideFullImg()" v-bind:style="setTop">
            <img v-bind:src="fullImg()" >
        </div>
        <!-- 八張圖片區 -->
        <div class="imgBody" v-for="(item, index) in src" :key="index"  v-bind:class="setMargin(index)">
            <!-- 加加動畫區 -->
            <div><img src="../../assets/add.png" v-on:click="showFullImg(index)"></div>
            <img v-bind:src= item>
        </div>
    </div>
</template>


<script>

import html from '../../assets/html.png'
import css from '../../assets/css.png'
import js from '../../assets/js.png'
import vue from '../../assets/logo.png'
import android from '../../assets/android-studio.png'
import c_language from '../../assets/c-language.png'
import c_sharp from '../../assets/c-sharp.png'
import java from '../../assets/java.png'

export default {
    data () {
        return {
            src:[html,css,js,vue,java,android,c_language,c_sharp],//圖片區
            showFull:false,//控制滿版圖片要不要出現
            clickImg:html,//存當下點擊的圖片是什麼
            marginIndex:[0,4],//要有margin的圖片框index
        }
    },
    props: ['content'],
    mounted(){
        // 監聽網頁放大縮小
        window.addEventListener("resize",this.handleResize)
    },
    methods: {
        // 如果縮到要更改版面配置 就得連margin一起變更
        handleResize(){
            if(window.innerWidth < 940){this.marginIndex = [0,2,4,6]}
            else{this.marginIndex = [0,4]}
        },
        // 除了第一或第二層的第二個外 都要加marginLeft
        setMargin(index){
            if(!this.marginIndex.includes(index)){return "imgBody-margin"}
            else{return ""}
        },
        // 點擊加號出現滿版圖片
        showFullImg(index){
            this.clickImg = this.src[index];
            this.showFull = true;
            var mo=function(e){e.preventDefault();};//這是啥??
            document.body.style.overflow='hidden';//隱藏滾動條
            document.addEventListener("touchmove",mo,false);//禁止頁面滑動
        },
        // 引楊藏滿版圖片
        hideFullImg(){
            this.showFull = false;
            var mo=function(e){e.preventDefault();};//這是啥??
            document.body.style.overflow='';//出現滾動條
            document.removeEventListener("touchmove",mo,false);//禁止頁面滑動
        },
        // 用來更換滿版圖片
        fullImg(){
            return this.clickImg;
        }
    },
    computed: {
        // 滿版圖片要出現在當下使用者的觀看位置
        setTop(){
            if(this.showFull == true){return{top: window.pageYOffset + 'px'}}
            else{return{top: 0}}
        },
    },
}
</script>


<style scoped>
/* 水平排列 設置超過換行 才能顯示兩排 並設置最小高度*/
.content-body{
    display: flex;
    flex-wrap: wrap;
    min-height: 45vw;
    background: #fff;
    color: #2c3e50;
    margin-left: 4vw;
    margin-right: 4vw;
}
/* 滿版圖片區 垂直和水平接置中 設置z-index才可以在螢幕最前 */
.full-screen{
    position: absolute;
    left: 0;
    height: 100vh;
    width: 100vw;
  
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 2;
  
    display: flex;
    justify-content: center;
    align-items: center;
}
/* 滿版圖片大小 */
.full-screen > img{
    height: 30vw;
    width: 35vw;
}
/* 水平排列 主軸和交錯軸都置中 位置相對給子元素參照 */
.imgBody{
    display: flex;
    margin-bottom: 1%;
    flex-basis: 23.25%;
    justify-content: center;
    align-items: center;
    background: #f8f8ff;
    position: relative;
}
/* 設置八張圖片的間隔 */
.imgBody-margin{
    margin-left: 2%;
}
/* 設置圖片寬高 */
img{
    width: 66%;
    height: 60%;
}
/* 設置動畫的出現區域 position absolute 參照父元素 */
.imgBody > div {
    position: absolute;
    display: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}
/* 這個東西觸發 透明度動畫*/
.imgBody:hover > div{
    display: flex;
    background: #00fa9a;
    opacity: 0.8;
    animation: fade-in;/*动画名称*/
    animation-duration: 0.5s;
}
/* 出現動畫 由小到大 */
.imgBody > div > img{
    width: 20%;
    height: 20%;
    cursor: pointer;
    animation: bump-in;/*动画名称*/
    animation-duration: 0.5s;
}


/* RWD 製作 */
@media all and (max-width: 940px){
    .imgBody{
        flex-basis: 48%;
        height: 33vw;
    }
    img{
        width: 63%;
        height: 73%;
    }
}
</style>
