<template>
    <div class="display-body">
        <fullImg v-bind:content="fullContent"></fullImg>
        <div class="imgBody" v-for="(item, index) in content" :key="index">
            <div>
                <p class="img-title" v-on:click="showFullImg(index)">{{item.imgTitle}}</p>
                <p class="img-text" v-on:click="showFullImg(index)">{{item.imgText}}</p>
            </div>
            <img v-bind:src= item.imgSrc>
            <!-- <img v-lazy= item.imgSrc> -->
        </div>
    </div>
</template>


<script>

import fullImg from './displayFull'

export default {
    data () {
        return {
            fullContent:{clickImg:null,show:false},
            src:this.content.imgSrc,//圖片區
            // marginIndex:[0,4],//要有margin的圖片框index
        }
    },
    props: ['content'],
    mounted(){
        // 監聽網頁放大縮小
        // window.addEventListener("resize",this.handleResize)
    },
    methods: {
        showFullImg(index){
            this.fullContent.clickImg = this.content[index].imgSrc;
            this.fullContent.show = true;
            var mo=function(e){e.preventDefault();};//這是啥??
            document.body.style.overflow='hidden';//隱藏滾動條
            document.addEventListener("touchmove",mo,false);//禁止頁面滑動
        },
        // // 如果縮到要更改版面配置 就得連margin一起變更
        // handleResize(){
        //     if(window.innerWidth < 1280){this.marginIndex = [0,2,4,6]}
        //     else{this.marginIndex = [0,4]}
        // },
        // // 除了第一或第二層的第二個外 都要加marginLeft
        // setMargin(index){
        //     if(!this.marginIndex.includes(index)){return "imgBody-margin"}
        //     else{return ""}
        // },
    },
    components: {
        fullImg,
    },
}
</script>


<style scoped>
.display-body{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: 35vw;
    width: 90%;
    background: #fff;
    color: #2c3e50;
    margin:2vw 1vw 0vw 1vw;
}
.imgBody{
    display: flex;
    margin-bottom: 1%;
    flex-basis: 19%;
    height: 17vw;
    justify-content: center;
    align-items: center;
    background: #f5deb3;
    position: relative;
    margin-left: 2%;
    overflow: hidden;
}
.imgBody:nth-of-type(1){
    margin-left: 0;
}
.imgBody:nth-of-type(5){
    margin-left: 0;
}
img{
    width: 55%;
    height: 95%;
    transition: all 0.3s ease-in;
}
.imgBody:hover img{
    transform:scale(1.5,1.5);
}
.imgBody > div {
    position: absolute;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.3s ease-in;
    z-index: 1;
}
.imgBody:hover > div{
    display: flex;
    flex-direction: column;
    background: #90ee90;
    opacity: 0.8;
    transform:scale(2,2);
}
.img-title{
    display: flex;
    white-space: nowrap;
    font-size: 1vw;
    font-family: 'Literata',"微软雅黑",Arial,'serif';
    cursor: pointer;
}
.img-text{
    display: flex;
    white-space: nowrap;
    font-size: 0.5vw;
    font-family: "微软雅黑",Arial,sans-serif;
    cursor: pointer;
}
/* RWD 製作 */
@media all and (max-width: 1280px){
    .display-body{
        margin-top: 0vw;
    }
    .imgBody{
        flex-basis: 35%;
        height: 30vw;
        margin-top: 0;
    }
    img{
        width: 55%;
        height: 90%;
    }
    .imgBody:nth-of-type(1){
        margin-left: 0;
    }
    .imgBody:nth-of-type(3){
        margin-left: 0;
    }
    .imgBody:nth-of-type(5){
        margin-left: 0;
    }
    .imgBody:nth-of-type(7){
        margin-left: 0;
    }
    .img-title{
        font-size: 1.8vw;
    }
    .img-text{
        font-size: 1.3vw;
    }
}
@media all and (max-width: 880px){
    .display-body{
        margin-top: 0vw;
    }
    .imgBody{
        flex-basis: 58%;
        height: 48vw;
        margin-left: 0;
    }
    img{
        width: 55%;
        height: 90%;
    }
    .img-title{
        font-size: 2.3vw;
    }
    .img-text{
        font-size: 1.8vw;
    }
}

</style>
