<template>
  <div id="slider">
    <!-- 判斷滑鼠鼠標是否在上面來決定要不要播放 -->
    <div class="window" @mouseover="stop" @mouseleave="play">
      <!-- 移動的是下面這個 -->
      <ul class="container" :style="containerStyle">
        <!-- 無限輪播的第一張輔助圖 -->
        <li class="content">  
          <img :src="sliders[sliders.length - 1].img" alt="">
        </li>
        <!-- 無限輪播的主要圖片 -->
        <li class="content" v-for="(item, index) in sliders" :key="index">  
          <img :src="item.img" alt="">
        </li>
        <!-- 無限輪播的最後一張輔助圖 -->
        <li class="content">  
          <img :src="sliders[0].img" alt="">
        </li>
      </ul>
      <!-- 左右按鈕區 -->
      <ul class="direction">  
        <li class="left" @click="move(contentWidth,1,speed)">
          <svg class="left-icon" width="2.5vw" height="2.5vw" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#ffffff" d="M481.233 904c8.189 0 16.379-3.124 22.628-9.372 12.496-12.497 12.496-32.759 0-45.256L166.488 512l337.373-337.373c12.496-12.497 12.496-32.758 0-45.255-12.498-12.497-32.758-12.497-45.256 0l-360 360c-12.496 12.497-12.496 32.758 0 45.255l360 360c6.249 6.249 14.439 9.373 22.628 9.373z"  /></svg>          
        </li>
        <li class="right" @click="move(contentWidth,-1,speed)">
          <svg class="right-icon" width="2.5vw" height="2.5vw" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"><path fill="#ffffff" d="M557.179 904c-8.189 0-16.379-3.124-22.628-9.372-12.496-12.497-12.496-32.759 0-45.256L871.924 512 534.551 174.627c-12.496-12.497-12.496-32.758 0-45.255 12.498-12.497 32.758-12.497 45.256 0l360 360c12.496 12.497 12.496 32.758 0 45.255l-360 360c-6.249 6.249-14.439 9.373-22.628 9.373z"  /></svg>          
        </li>
      </ul>
      <!-- 底下點點區 -->
      <ul class="dots"> 
        <li v-for="(dot, i) in sliders" :key="i" 
        :class="{dotted: i === (currentIndex-1)}"
        @click = jump(i+1)>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

import bg from '../../assets/slide-bg.jpg'
import bg2 from '../../assets/slide-bg2.jpeg'
import bg3 from '../../assets/slide-bg3.jpg'
import bg4 from '../../assets/slide-bg4.jpg'
import bg5 from '../../assets/slide-bg5.jpg'

export default {
  name: 'slider',
  props: {
    initialSpeed: {
      type: Number,
      default: 40
    },
    initialInterval: {
      type: Number,
      default: 4
    }
  },
  data () {
    return {
      sliders:[{img:bg},{img:bg2},{img:bg3},{img:bg4},{img:bg5}], //存圖片
      currentIndex:1, //存現在到哪一張圖片
      contentWidth:window.innerWidth, //存視窗寬度
      distance:-(window.innerWidth), //存現在到哪一個圖片位置了 會隨著圖片移動而變化
      transitionEnd: true, //移動的開關 判斷哪些時候可以移動
      speed: this.initialSpeed, //移動速度
    }
  },
  computed:{
    containerStyle() {
      return {
        transform:`translate3d(${this.distance}px, 0, 0)` //這個東西是可以讓元件依照XYZ軸去做移動
      }
    },
    interval() {
      return this.initialInterval * 1000 //這是計算幾秒自動滑一頁的方法
    }
  },
  mounted() {
      this.init()
      this.distance = -(window.innerWidth); //一開始是第二張圖(第一張為無限輪播的輔助)
      window.onresize = () =>{
        this.stop()//當網頁大小在變動時 要停止 不然會打亂移動規律
        this.distance = -(window.innerWidth) * this.currentIndex; //監聽網頁大小的變動來更改distance的數值
        this.contentWidth = window.innerWidth //監聽網頁大小的變動來改變移動的大小
      };
  },
  methods:{
      init(){
        this.play() //直接開始自動輪播
        window.onblur = function() { this.stop() }.bind(this) //失去焦點
        window.onfocus = function() { this.play() }.bind(this) //得到焦點
       },
      move(offset,direction,speed) {
        if (!this.transitionEnd) return  //这里是闸 用來開關可不可以進行移動 以防連續按到移動按鈕
        this.transitionEnd = false //這邊要關閉開關 避免連續按
        // 判斷是要往右還是往左 並連jump的情況一起判斷移動距離
        direction === -1 ? this.currentIndex += offset/this.contentWidth : this.currentIndex -= offset/this.contentWidth
        if (this.currentIndex > 5) this.currentIndex = 1
        if (this.currentIndex < 1) this.currentIndex = 5

        const destination = this.distance + offset * direction
        this.animate(destination,direction,speed) //移動動畫
      },
      animate(des, direc,speed) {
        // 不太懂的地方 this.temp 是指什麼?
        if (this.temp) { 
          window.clearInterval(this.temp)
          this.temp = null 
        }
        this.temp = window.setInterval(() => {
          //每10毫秒持續移動 以speed代表移動速度
            if ((direc === -1 && des < this.distance - speed) || (direc === 1 && des > this.distance + speed)) {
                this.distance += speed * direc 
            } else {
              // 移動完成後 還有開啟開關 關閉連續調用 更新distance 並判斷是否超過界線
                this.transitionEnd = true
                window.clearInterval(this.temp)
                this.distance = des
                if (des < -(window.innerWidth * 5) ) this.distance = -(window.innerWidth)
                if (des > -(window.innerWidth)) this.distance = -(window.innerWidth * 5)
            }
        }, 10)
      },
      jump(index) {
        const direction = index - this.currentIndex >= 0 ? -1 : 1 //判斷往左往右 以 -1 1 代替
        const offset = Math.abs(index - this.currentIndex) * window.innerWidth //計算移動距離
        // 計算移動速度
        const jumpSpeed = Math.abs(index - this.currentIndex) === 0 ? this.speed : Math.abs(index - this.currentIndex) * this.speed 
        this.move(offset, direction, jumpSpeed)
      },
      play() {
        // 每次調用interval都要先clear
        if (this.timer) {
            window.clearInterval(this.timer)
            this.timer = null
        }
        this.timer = window.setInterval(() => {
            this.move(this.contentWidth, -1, this.speed)
        }, this.interval)
      },
      stop() {
        window.clearInterval(this.timer)
        this.timer = null
      }
    }
}
</script>

<style scoped>
ol,ul{
  list-style: none;
}
#slider{
  /* display: flex; */
  text-align: center;
}
/* overflow:hiden 自動隱藏超出的文字或圖片 並設置position :relative 給container參照*/
.window{
  position:relative;
  flex-basis: 100vw;
  height: 95vh;
  max-height: 47vw;
  margin:0 auto;
  overflow:hidden;
}
/* 讓圖片並排  並設置position :absolute 參照window*/
.container{
  display:flex;
  /* flex-basis: 100%; */
  /* height: 50vw; */
  position:absolute;
}
img{
  user-select: none;
  height: 48vw;
  width: 100vw;
}
/* .content{
  flex-basis: 100%;
  height: 50vw;
} */

/* 兩個按鈕 */
.left, .right{
  position:absolute;
  top:50%;
  transform:translateY(-50%);
  width:3.5vw;
  height:3.5vw;
  background-color:rgba(0,0,0,.3);
  border-radius:50%;
  cursor:pointer;
}
/* 左按鈕位置 */
.left{
  left:3%;
  padding-left:12px;
  padding-top:10px;
}
.left-icon{
  position:absolute;
  top:20%;
  left:25%;
}
/* 右按鈕位置 */
.right{
  right:3%;
  padding-right:12px;
  padding-top:10px;
}
.right-icon{
  position:absolute;
  top:20%;
  right:25%;
}
/* 點點位置 */
.dots{
    position:absolute;
    bottom:10%;
    left:50%;
    transform:translateX(-50%);
  }
  /* 點點一般的樣子 */
.dots li{
  display:inline-block;
  width:15px;
  height:15px;
  margin:0 3px;
  border:1px solid white;
  border-radius:50%;
  background-color:#333;
  cursor:pointer;
}
/* currentIndex的點點樣式 */
.dots .dotted{
  background-color:orange;
}
/* 當小到某一程度時要變成側滑欄 */
@media all and (max-width: 1080px){
    .left-icon{
      width: 4vw;
      height: 4vw;
      top:15%;
    }
    .right-icon{
      width: 4vw;
      height: 4vw;
      top:15%;
    }
    .left{
      width: 5.2vw;
      height: 5.2vw;
    }
    .right{
      width: 5.2vw;
      height: 5.2vw;
    }
}
</style>