<template>
    <article>
        <component v-for="(item, index) in divContent" :key="index" :is="chooseType(index)" v-bind:content="item"></component>
    </article>
</template>


<script>

import autobiography from '../contentType/Autobiography/Autobiography'
import expertise from '../contentType/Expertise/Expertise'
import interest from '../contentType/interest/interest'
import portfolio from '../contentType/portfolio/Portfolio'
import album from '../contentType/Album/Album'
import connect from '../contentType/connectMe/connect'

export default {
    data () {
        return {
            // 由上到下的網頁樣式
            divList:["autobiography","expertise","interest","portfolio","album","connect"],
             // 要傳給樣式Vue的物件
            divContent:[
                {type:"autobiography"},{type:"expertise"},
                {type:"interest"},{type:"portfolio"},
                {type:"album"},{type:"connect"},
            ],
        }
    },
    methods: {
        chooseType(index){
            return this.divList[index];
        }
    },
    components:{
        autobiography,
        expertise,
        interest,
        portfolio,
        album,
        connect
    }
}
</script>

<style scoped>
.content-body{
    color: black;
}
</style>

